.page-header--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 6px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000; }

