@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  /* Браузер сначала попробует найти шрифт локально */
  src: local("Oswald"),
    /* Если не получилось, загрузит woff2 */
    url("../fonts/oswaldregular.woff2") format("woff2"),
    /* Если браузер не поддерживает woff2, загрузит woff */
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  /* Браузер сначала попробует найти шрифт локально */
  src: local("Oswald"),
    /* Если не получилось, загрузит woff2 */
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    /* Если браузер не поддерживает woff2, загрузит woff */
    url("../fonts/oswaldmedium.woff") format("woff");
}

html {
  overflow-x: hidden;
}

body {
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-align: center;
  color: #000000;
  background-color: #ffffff;
  min-height: 100%; /*займи все пространство*/
}

h1 {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    text-align: left;
    font-size: 60px;
    line-height: 60px;
    padding-left: 87px;
    width: 414px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 80px;
  }
}
